@font-face {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1210px;
  margin: 0 auto;
  padding-left: 25px;
  padding-right: 25px;
  box-sizing: border-box;
}

@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 767px;
    min-width: 320px;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  width: 100%;
  font-size: 16px;
  line-height: 19.5px;
  color: #f9fbfd;
  position: absolute;
  top: 0;
  left: 0;
}

.main-header__inner-wrap {
  width: 100%;
  margin: 0 auto;
  padding-top: 35px;
  padding-bottom: 35px;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 1023px) {
  .main-header__inner-wrap {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .main-header__logo-toggle-wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  .main-header__inner-wrap {
    padding-top: 20px;
  }
  .main-header--nav-opened .main-header__inner-wrap {
    width: calc(100% - 35px);
    min-height: 526px;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    background-color: #f9fbfd;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
  }
  .main-header--nav-opened .main-header__logo-toggle-wrap {
    margin-bottom: 33px;
    padding-left: 15px;
    padding-right: 15px;
  }
  .main-header--nav-opened .main-header__navigation {
    display: block;
    margin-bottom: auto;
  }
  .main-header--nav-closed .main-header__navigation {
    display: none;
  }
}
.main-content {
  width: 100%;
  margin-top: 0;
}

.main-content__strengths {
  margin: 0;
}

.footer {
  width: 100%;
  color: #f9fbfd;
  background-color: #011c40;
}

.footer__wrap {
  margin: 0 auto;
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
  gap: 15px;
}

@media (max-width: 1023px) {
  .footer__wrap {
    gap: 10px;
  }
  .footer__wrap {
    padding-top: 45px;
    padding-bottom: 44px;
  }
}
@media (max-width: 767px) {
  .footer__wrap {
    padding-top: 40px;
    padding-bottom: 31px;
    flex-direction: column;
  }
  .footer__inner-logo-wrap {
    margin-bottom: 11px;
  }
  .footer__navigation {
    margin-bottom: 0;
  }
}
.user-navigation {
  display: flex;
}

.user-navigation__list {
  margin: auto 0 0 auto;
  padding: 0;
  padding-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  width: max-content;
  max-width: 1080px;
  list-style-type: none;
}

.user-navigation__item {
  margin-left: 33px;
}

.user-navigation__item a {
  font-size: 16px;
  font-weight: 500;
  line-height: 19.5px;
  color: #f9fbfd;
  text-decoration: none;
  cursor: pointer;
}

.user-navigation--bottom a {
  color: #f9fbfd;
}

.user-navigation--bottom .user-navigation__item:first-child {
  margin-left: 25px;
}

.user-navigation--bottom .user-navigation__list {
  padding-bottom: 5px;
}

.user-navigation__item a:hover {
  color: #d8d8d9;
}

@media (max-width: 1023px) {
  .user-navigation__item a {
    font-size: 14px;
    line-height: 17px;
  }
  .user-navigation--bottom a {
    font-size: 12px;
    line-height: 14.5px;
  }
  .user-navigation__item,
.user-navigation--bottom .user-navigation__item {
    margin-left: 23px;
  }
  .user-navigation--bottom .user-navigation__item:first-child {
    margin-left: 15px;
  }
}
@media (max-width: 767px) {
  .user-navigation__list {
    flex-direction: column;
    color: #011c40;
    z-index: 10;
  }
  .user-navigation__item {
    margin-bottom: 10px;
  }
  .user-navigation__item a {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
  .user-navigation--bottom ul {
    margin: 0 auto 0 0;
  }
  .user-navigation--bottom .user-navigation__item {
    margin: 0 auto 0 0;
  }
  .user-navigation--bottom .user-navigation__item:first-child {
    margin-left: 0;
  }
  .user-navigation--bottom .user-navigation__item:not(:last-child) {
    margin-bottom: 9px;
  }
  .user-navigation--bottom a {
    font-size: 12px;
    line-height: 14.5px;
    color: #f9fbfd;
  }
}
.page body {
  height: 100%;
  font-family: "Montserrat", sans-serif, "Arial";
  font-size: 16px;
  line-height: 24px;
  color: #011c40;
  background-color: #f9fbfd;
  position: relative;
}

.page header {
  margin: 0;
}

@media (max-width: 767px) {
  .page body {
    min-width: 320px;
  }
}
.contacts {
  padding-top: 120px;
  padding-bottom: 120px;
}

.contacts__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}

.contacts__map {
  grid-column: 1/2;
}

.contacts__map img,
.contacts__map picture {
  width: 100%;
  height: auto;
  display: block;
}

.contacts__text {
  margin: 0 0 0 auto;
  display: flex;
  grid-column: 2/3;
}

.contacts__text-inner-wrap {
  height: min-content;
  max-width: 500px;
  margin: auto auto auto 0;
  padding-right: 10px;
  padding-top: 2px;
}

.contacts__text b {
  max-width: 500px;
  margin: 0 auto 32px 0;
  padding: 0;
  display: block;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  color: #011c40;
  text-align: left;
}

.contacts__text span,
.contacts__text a {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #011c40;
  text-align: left;
}

.contacts__text a {
  display: flex;
  text-decoration: none;
}

.contacts__text p {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  display: flex;
}

.contacts__text a:hover span {
  color: #3384af;
}

.contacts__text a:hover svg {
  fill: #3384af;
}

.contacts__text br {
  display: none;
}

.contacts__text svg {
  margin-right: 10px;
  display: block;
  fill: #011c40;
}

@media (max-width: 1023px) {
  .contacts {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .contacts__wrap {
    gap: 30px;
  }
  .contacts__wrap br {
    display: none;
  }
  .contacts__map img,
.contacts__map picture {
    min-width: 324px;
  }
  .contacts__address svg {
    width: 18px;
    height: 22px;
  }
}
@media (max-width: 767px) {
  .contacts {
    padding-top: 55px;
    padding-bottom: 70px;
  }
  .contacts__wrap {
    display: flex;
    flex-direction: column;
  }
  .contacts__text-inner-wrap {
    max-width: 100%;
    margin: 0 auto;
    padding-right: 0;
  }
  .contacts__text {
    width: 100%;
    margin: 0 auto;
  }
  .contacts__map {
    order: 2;
  }
  .contacts__map img,
.contacts__map picture {
    min-width: 100%;
  }
  .contacts__text span br {
    display: block;
  }
  .contacts__text b {
    margin-bottom: 29px;
  }
}
.cruises {
  padding-top: 80px;
  padding-bottom: 120px;
}

.cruises h2 {
  margin: 0 auto 48px auto;
  padding: 0;
  font-size: 28px;
  font-weight: 600;
  line-height: 48px;
  color: #011c40;
  text-align: center;
}

.cruises__list {
  max-width: 1130px;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: auto;
  gap: 30px;
  align-content: stretch;
}

.cruises__item {
  max-width: 260px;
  position: relative;
}

.cruises__item h3 {
  max-width: 210px;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  color: #f9fbfd;
  text-align: left;
  position: absolute;
  bottom: 5px;
  left: 30px;
  z-index: 5;
}

.cruises__item img,
.cruises__item picture {
  width: auto;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

.cruises__item .cruises__inner-wrap {
  display: none;
}

.cruises__item:hover,
.cruises__item:focus-within,
.cruises__item--check {
  height: 386px;
  padding: 30px 25px 35px 25px;
  display: flex;
  flex-direction: column;
  background-color: #011c40;
  box-shadow: 35px 0px, 4px rgba(1, 28, 64, 0.15);
  overflow-y: auto;
  box-shadow: 0 4px 35px 0 rgba(1, 28, 64, 0.15);
}

.cruises__item:hover img,
.cruises__item:hover picture,
.cruises__item:focus-within img,
.cruises__item:focus-within picture,
.cruises__item--check img,
.cruises__item--check picture {
  display: none;
