.button {
  width: 100%;
  height: min-content;
  padding: 10px;
  display: block;
  font-size: 14px;
  font-weight: 600;
  line-height: 21px;
  color: #011c40;
  text-align: center;
  text-decoration: none;
  background-color: #f9fbfd;
  cursor: pointer;
}

.button:hover {
  background-color: #d8d8d9;
}

.toggle {
  display: none;
}

@media (max-width: 767px) {
  .toggle {
    display: flex;
  }
  .toggle-button {
    width: min-content;
    height: min-content;
    margin-top: auto;
    margin-left: auto;
    margin-right: 0;
    padding: 0;
    padding-bottom: 3px;
    border-width: 0;
    background-color: transparent;
    cursor: pointer;
  }
  .toggle--closed .toggle__burger-svg {
    display: block;
  }
  .toggle--closed .toggle__cross {
    display: none;
  }
  .toggle--opened .toggle__burger-svg {
    display: none;
  }
  .toggle--opened .toggle__cross {
    display: block;
  }
  .toggle-button .toggle__burger-svg {
    stroke: #f9fbfd;
    stroke-width: 2px;
  }
  .toggle-button .toggle__cross {
    stroke: #011c40;
    stroke-width: 2px;
  }
  .toggle-button:hover .toggle__burger-svg,
.toggle-button:hover .toggle__cross {
    stroke: #d8d8d9;
    stroke-width: 2px;
  }
  .toggle-button:active .toggle__burger-svg,
.toggle-button:active .toggle__cross {
    stroke: #e3ecf6;
    stroke-width: 2px;
  }
}
.input-textarea-wrap {
  position: relative;
}

.input-textarea-wrap input,
.input-textarea-wrap textarea {
  height: min-content;
  padding: 11px 15px;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif, "Arial";
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
  border-width: 0;
  cursor: text;
}

.input-textarea-wrap input::placeholder,
.input-textarea-wrap textarea::placeholder {
  font-family: "Montserrat", sans-serif, "Arial";
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.7);
}

.input-textarea-wrap input:hover,
.input-textarea-wrap textarea:hover {
  outline: 2px solid #bbd5e6;
}

.input-textarea-wrap input.valid {
  outline: 2px solid #2c39f2;
}

.input-textarea-wrap input.invalid {
  outline: 2px solid #811733;
}

.input-textarea-wrap em {
  display: none;
}

.input-textarea-wrap .input-textarea-wrap__show-tip {
  display: block;
  position: absolute;
  top: 53px;
  left: 5px;
  z-index: 5;
  font-family: "Montserrat", sans-serif, "Arial";
  font-size: 12px;
  line-height: 14px;
  font-style: normal;
  color: #811733;
}

@media (max-width: 767px) {
  .input-textarea-wrap textarea {
    padding-top: 24px;
  }
}
.consent-checkbox-wrap {
  width: 100%;
  padding-top: 5px;
  position: relative;
}

.consent-checkbox-wrap input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.consent-checkbox-wrap b {
  margin-left: 22px;
  display: block;
  font-size: 10px;
  font-weight: 400;
  line-height: 13px;
  color: #f9fbfd;
  text-align: left;
}

.consent-checkbox-wrap span {
  width: 18px;
  height: 18px;
  top: 0;
  left: 0;
  background-color: #f9fbfd;
  content: "";
  position: absolute;
  z-index: 3;
}

.consent-checkbox-wrap input:checked + span {
  background-image: url("../img/svg/tick.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.consent-checkbox-wrap label,
.consent-checkbox-wrap span,
.consent-checkbox-wrap input {
  cursor: pointer;
}

.consent-checkbox-wrap:hover b {
  color: #d8d8d9;
}

.consent-checkbox-wrap:hover span {
  border-color: #d8d8d9;
}

.consent-checkbox-wrap input:focus + span {
  outline: 2px solid #011c40;
}

